.stripe-card-container {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-pack: center;
        justify-content: center;
}

.center-stripe-card {
    -ms-flex-preferred-size: var(--content-width);
        flex-basis: var(--content-width);
    padding: 5px 15px;
}

.stripe-card-spacer {
    width: 15px;
}

@media only screen and (min-width: 840px) {
    .left-stripe-card,
    .right-stripe-card {
        -ms-flex-positive: 1;
            flex-grow: 1;
    }
}


